import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const router = new Router({
  mode: "history",
  base: process.env.BASE_URL,
  linkActiveClass: 'active',
  routes: [{
      path: '/login',
      name: 'login',
      component: () => import('../components/home/login.vue'),
    },
    {
      path: '/',
      name: 'index',
      component: () => import('../components/home/index.vue'),
      meta: {
        keepAlive: true
      },
      children: [{
          path: "",
          redirect: "/dynamic"
        },
        {
          path: "/dynamic",
          name: "dynamic",
          component: () => import('../views/dynamic.vue')
        },
        {
          path: "/build",
          name: "build",
          component: () => import('../views/build.vue')
        },
        {
          path: "/talk",
          name: "talk",
          component: () => import('../views/talk.vue')
        },
        {
          path: "/active",
          name: "active",
          component: () => import('../views/active.vue')
        },
        {
          path: "/difficult",
          name: "difficult",
          component: () => import('../views/difficult.vue')
        },
        {
          path: "/work",
          name: "work",
          component: () => import('../views/work.vue')
        },
        {
          path: "/volunteer",
          name: "volunteer",
          component: () => import('../views/volunteer.vue')
        },
      ]
    },
    {
      path: '/Dynamic-One',
      name: 'Dynamic-One',
      component: () => import('../components/home/dynamic-one.vue'),
    },
    {
      path: '/Build-Videoone',
      name: 'Build-Videoone',
      component: () => import('../components/home/build-video1.vue'),
    },
    {
      path: '/Active-One',
      name: 'Active-One',
      component: () => import('../components/home/active-one.vue'),
    },
    {
      path: '/Build-Picone',
      name: '/Build-Picone',
      component: () => import('../components/home/build-pic1.vue'),
    },
    {
      path: '/system',
      name: 'system',
      component: () => import('../components/system/system.vue'),
    },
    {
      path: "/organization",
      name: "organization",
      component: () => import('../components/system/organization.vue')
    },
    {
      path: "/organization-list",
      name: "organization-list",
      component: () => import('../components/system/organization-list.vue')
    },
    {
      path: "/unit",
      component: () => import('../components/system/unit.vue'),
      children: [{
          path: "",
          redirect: "/firm"
        },
        {
          path: "/firm",
          name: "firm",
          component: () => import('../components/system/firm.vue')
        },
        {
          path: "/company",
          name: "company",
          component: () => import('../components/system/company.vue')
        }
      ]
    },
    {
      path: "/firm-list",
      name: "firm-list",
      component: () => import('../components/system/firme-list.vue')
    },
    {
      path: "/member",
      // name: "member",
      component: () => import('../components/system/member.vue'),
      children: [{
          path: "",
          redirect: "/meberOne"
        },
        {
          path: "/meberOne",
          name: "meberOne",
          component: () => import('../components/system/member-one.vue')
        },
        {
          path: "/meberTwo",
          name: "meberTwo",
          component: () => import('../components/system/member-two.vue')
        }
      ]
    },
    {
      path: "/memberone-list",
      name: "memberone-list",
      component: () => import('../components/system/memberone-list.vue')
    },      
    {
      path: "/membertwo-list",
      name: "membertwo-list",
      component: () => import('../components/system/membertwo-list.vue')
    },
    {
      path: "/volunteers",
      // name: "volunteers",
      component: () => import('../components/system/volunteer.vue'),
      children: [{
          path: "",
          redirect: "/volunteeres"
        },
        {
          path: "/volunteeres",
          name: "volunteeres",
          component: () => import('../components/system/volunteers.vue')
        },
        {
          path: "/actives",
          name: "actives",
          component: () => import('../components/system/activity.vue')
        },
        {
          path: "/feedback",
          name: "feedback",
          component: () => import('../components/system/feedback.vue')
        },
      ]
    },
    {
      path: "/volunteers-list",
      name: "volunteers-list",
      component: () => import('../components/system/volunteers-list.vue')
    },
    {
      path: "/actives-list",
      name: "actives-list",
      component: () => import('../components/system/actives-list.vue')
    },
    {
      path: "/feedback-list",
      name: "feedback-list",
      component: () => import('../components/system/feadback-list.vue')
    },
    {
      path: "/society",
      // name: "society",
      component: () => import('../components/system/society.vue'),
      children: [{
          path: "",
          redirect: "/societyInstitution"
        },
        {
          path: "/societyInstitution",
          name: "societyInstitution",
          component: () => import('../components/system/society-institution.vue')
        },
        {
          path: "/societyActive",
          name: "societyActive",
          component: () => import('../components/system/society-activity.vue')
        },
        {
          path: "/societyBack",
          name: "societyBack",
          component: () => import('../components/system/society-back.vue')
        },
      ]
    },
    {
      path: "/societyInstitution-list",
      name: "societyInstitution-list",
      component: () => import('../components/system/societyInstitution-list.vue')
    },
    {
      path: "/societyActive-list",
      name: "societyActive-list",
      component: () => import('../components/system/societyActive-list.vue')
    },
    {
      path: "/societyBack-list",
      name: "societyBack-list",
      component: () => import('../components/system/societyBack-list.vue')
    },
    {
      path: "/hp",
      // name: "hp",
      component: () => import('../components/system/hp.vue'),
      children: [{
          path: "",
          redirect: "/hpActive"
        },
        {
          path: "/hpActive",
          name: "hpActive",
          component: () => import('../components/system/hp-active.vue')
        },
        {
          path: "/hpMange",
          name: "hpMange",
          component: () => import('../components/system/hp-mange.vue')
        }
      ]
    },
    {
      path: "/hpMange-list",
      name: "hpMange-list",
      component: () => import('../components/system/hpMange-list.vue')
    },
    {
      path: "/works",
      name: "works",
      component: () => import('../components/system/work.vue')
    },
    {
      path: "/works-list",
      name: "works-list",
      component: () => import('../components/system/works-list.vue')
    },
    {
      path: "/statistics",
      name: "statistics",
      component: () => import('../components/statistics/statistics.vue')
    },
    {
      path: "/Member-statistics",
      name: "Member-statistics",
      component: () => import('../components/statistics/Member-statistics.vue')
    },
    {
      path: "/Difficult-statistics",
      name: "Difficult-statistics",
      component: () => import('../components/statistics/difficult-statistics.vue')
    },
    {
      path: "/Model-statistics",
      name: "Model-statistics",
      component: () => import('../components/statistics/model-statistics.vue')
    },
    {
      path: "/Volunteer-statistics",
      name: "Volunteer-statistics",
      component: () => import('../components/statistics/volunteer-statistics.vue')
    },
    {
      path: "/Society-statistics",
      name: "Society-statistics",
      component: () => import('../components/statistics/society-statistics.vue')
    },
    {
      path: "/Hp-statistics",
      name: "Hp-statistics",
      component: () => import('../components/statistics/hp-statistics.vue')
    },
    {
      path: "/Work-statistics",
      name: "Work-statistics",
      component: () => import('../components/statistics/work-statistics.vue')
    },
    {
      path: "/My",
      name: "My",
      component: () => import('../components/my/my.vue')
    },
    {
      path: "/My-Self",
      name: "My-Self",
      component: () => import('../components/my/my-self.vue')
    },
  ]
})

// router.beforeEach((to, from, next) => {
//   const isLogin = localStorage.ele_login ? true : false;
//   if (to.path == "/login") {
//     next();
//   } else {
//     //是否登录状态下
//     isLogin ? next() : next("/login");
//   }
// });
export default router;



